.features-desc {
  position: relative;
}

.fcontent-text {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  padding: 10px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.fdesc-item:hover .fcontent-text {
  opacity: 1;
}

.features-desc-item {
  display: flex;
  flex-direction: column;
  align-items: center;}
.features-desc-wrapper {
  display: flex;
  flex-wrap: wrap; /* Permet aux éléments de passer à la ligne si l'espace est insuffisant */
  justify-content: center;
  justify-content: center; 
  gap: 20px;
}

.fdesc-item {
  max-width: 300px;
  text-align: center;
  position: relative;
  overflow: hidden;
  align-items: center; /* Centre le contenu horizontalement */
}

.fimagecontainer {
  width: 300px;
  height: 200px;
  overflow: hidden;
}

.vaccination-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.fimagecontainer .fcolor {
  width: 100%;
  height: 100%;
  position: relative;
}

.fimagecontainer .fcolor img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease-in-out;
}

.fimagecontainer:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.fimagecontainer:hover:before {
  opacity: 1;
}

.fimagecontainer:hover img {
  transform: scale(1.1);
}

.right {
  text-align: center;
  width: 100%; /* Assure que le bouton prend toute la largeur du conteneur */
}
